














import Vue from "vue";
import {api} from "@/ts/constants";

export default Vue.extend({
    name: "Settings",
    data: () => ({
        apiUrlChanged: false,
        rotating: false,
    }),
    methods: {
        reload() {
            this.rotating=true;
            location.reload();
        },
    },
    computed: {
        reloadNeeded() {
            return this.apiUrlChanged;
        },
        apiUrl: {
            get(): string {
                return api;
            },
            set(v: string) {
                localStorage.api = v;
                this.apiUrlChanged = v !== api;
            },
        },
        mapboxKey: {
            get(): string {
                return this.$store.state.mapboxKey;
            },
            set(v: string) {
                this.$store.commit('mapboxKey', v);
            },
        },
    }
});
